/* montserrat-500 - latin_cyrillic */
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local('Montserrat Medium'), local('Montserrat-Medium'),
      url('../fonts/montserrat-v12-latin_cyrillic-ext-500.woff2') format('woff2'), /* Super Modern Browsers */
      url('../fonts/montserrat-v12-latin_cyrillic-ext-500.woff') format('woff'), /* Modern Browsers */
}

/* montserrat-500italic - latin_cyrillic */
@font-face {
  font-family: 'Montserrat';
  font-style: italic;
  font-weight: 500;
  font-display: swap;
  src: local('Montserrat Medium Italic'), local('Montserrat-MediumItalic'),
      url('../fonts/montserrat-v12-latin_cyrillic-ext-500italic.woff2') format('woff2'), /* Super Modern Browsers */
      url('../fonts/montserrat-v12-latin_cyrillic-ext-500italic.woff') format('woff'), /* Modern Browsers */
}

/* montserrat-700 - latin_cyrillic */
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local('Montserrat Bold'), local('Montserrat-Bold'),
      url('../fonts/montserrat-v12-latin_cyrillic-ext-700.woff2') format('woff2'), /* Super Modern Browsers */
      url('../fonts/montserrat-v12-latin_cyrillic-ext-700.woff') format('woff'), /* Modern Browsers */
}

/* Mon Amour One Medium-550 - latin_cyrillic */
@font-face {
  font-family: "Mon Amour One Medium";
  font-weight: normal;
  font-style: normal;
  src: local('Mon Amour One Medium'), local('Mon-Amour-One-Medium'),
    url("../fonts/mon-amour-one-medium.woff") format("woff"), /* Modern Browsers */
    url("../fonts/mon-amour-one-medium.woff2") format("woff2"), /* Modern Browsers */
}
